import CommentSlider from "../slider/comment-slider";
import { CommentContainer } from "./comment.style";

const Comment = () => {
    return (
        <CommentContainer>
            <div className="comment-title">
                <h1> <span></span></h1>
            </div>
            <div className="comment-slider">
                <CommentSlider />
            </div>
        </CommentContainer>
    );
}
 
export default Comment
